@font-face {
  font-family: Poppins;
  src: url(../public/Poppins-Regular.ttf)
}

.Poppins{
  font-family: 'Poppins';
}

*{
  margin:0;
  padding:0;
}

body{
  background-color: #000;
  color: #fff;
  font-family: '';
  font-family: 'Poppins';
}

.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: #00ff00;
  transform-origin: 0%;
}

.nav-bar{
  display: flex;
  justify-content: space-between;
}

.nav-elements{
  align-items: center;
  display: flex;
  gap:20px;
}

.nav-links{
  position:relative;
  width:max-content;
  text-decoration: none;
  list-style: none;
  cursor: pointer;
}

.home{
  height: 100vh;
}
.nav-links:after{
  content:"";
  left:0;
  height: 3px;
  position:absolute;
  width:0;
  background-color: #87ceed;
  bottom:-10px;
  transition-duration: 1000ms;
  cursor: pointer;
}

.nav-links:hover:after{
  width:100%;
}

.logo-txt{
  color: #87ceed;
  font-weight:500px;
  font-size: 30px;
  cursor: pointer;
  font-family: sans-serif;
}

.o{
  color:#00ff00;
  font-weight: 900;
  font-size: 50px;
}

.cart{
  width:30px;
  border:none;
  border-radius: 30px;
  cursor: pointer;
  position: relative;
  left: -20px;
  top:20px;
}

.mob-nav ul{
  display: flex;
  flex-direction: column;
  position: relative;
}

.mob-nav{
  list-style: none;
  text-decoration: none;
  cursor:pointer;
  transition-duration: 0.3s;
}

.mob-nav li:hover{
  font-size: 24px;
}

@media  screen and (max-width:600px) {
  .nav-links{
    display: none;
  }
}

@media  screen and (min-width:600px){
  .mob-nav{
    display: none;
  }
}

@keyframes appear{
  from{
    opacity:0.5;
    scale:0.5;
  }
  to{
    opacity: 1;
    scale:1;
  }
}

.home-disp{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding:2%;
  padding-top: 3%;
  animation:appear 5s linear;
  animation-timeline: view();
}

.home-disp button{
  opacity: 1;
}


.home-disp img{
  width: 27%;
}

.bt{
  width:100px;
  height: 40px;
  border-radius: 10px;
  border:none;
  background: #87ceed;
  cursor: pointer;
}

.bt:hover{
  opacity: 0.7;
  
}
.search-bar{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}

.search{
  width:400px;
  height:30px;
  border:none;
  border-radius: 30px;

}

.ser{
  color:white;
}

.explore{
  height: 100vh;
}


.boys-tshirt{
  height:250px;
  width:400px;
  display: flex;
  border-radius: 5px;
  gap:5px;
  padding: 0.3rem;
}

.boys-tshirt div{
  height: 100%;
  flex:1;
  overflow: hidden;
  cursor:pointer;
  border-radius: 2px;
  transition: all 0.7s;
  background: #212121;
  border:1px solid #87ceed;
  display: flex;
  align-items: center;
  justify-content: center;
}

.boys-tshirt div:hover{
  flex:4;
}
.boys-tshirt img{
  height:95%;
  margin-top: 15px;
}

@media screen and (max-width:600px) {
  .adult{
    display: block;
  }
}

.adult{
  display:flex;
  align-items: center;
  justify-content: space-evenly;
  padding-top: 3%;
}

.explore{
  text-align: center;
}

.kids-sec{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 4%;
}

.explore button{
  margin-top: 2%;
}


.mens-section{
  height: 80vh;
  padding: 3%;
  
}

.container{
  height: 500px;
  width:fit-content;
  background-color: #fff;
  border-radius: 20px;
  color: #000;
}
.cont{
  text-align: center;
}


.container img{
  height: 300px;
  border-radius: 20px;
}

.con{
  display: flex;
  align-items: center;
  justify-content:space-between ;
  padding-top:3%;
}



.rating{
  display: inline-block;
}

.rating input{
  display: none;
}

.rating label{
  float: right;
  cursor: pointer;
  color:#333;
}

.rating label:before{
  content: '\2605';
  font-size: 30px;
}

.rating input:checked~label{
  color: #e8ef08;
  border-color: #000;
}


.container{
  text-align: center;
  padding: 4%;
}

.container h1{
  font-size: 20px;
  padding-bottom: 2%;
}

.container h2{
  font-size: 22px;
  color: #00ff00;
  text-decoration: solid;
  font-weight: bold;
}

.og-price{
  color: red;
  padding-left: 3px;
}
.container .bt{
  margin-top: 7%;
}

.bt{
  transition-duration: 1s;
}
.bt:hover{
  box-shadow: 3px 5px 1px rgba(0,0,0,0.2);
}

 .me{
  display: flex;
  align-items: center;
  justify-content: space-between;
 }

 .mb{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0px;
 }
 .star{
  max-height: 20px;
 }

.price{
  padding-top: 5%;
}

 .rating{
  display: flex;
  align-items: center;
  justify-content: center;
 }

 .contact{
  position: relative;
  width:100%;
  padding:2rem;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
 }

 .form{
  width:100%;
  max-width: 820px;

  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px 1px rgba(0,0,0,0.1);
  z-index: 1000;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2,1fr);
 }


 .contact-form{
  background-color: #87ceed;
  position: relative;
 }

 .circle{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg,transparent 20%, #149279);
  position: absolute;
  top:130px;
  right:-40px;
 }



 .two{
  width:80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg,transparent 20%, #25afea);
  top:10px;
  right:-40px;
 }

 .contact-form:before{
  content:"";
  position: absolute;
  width: 26px;
  height: 26px;
  background-color:#87ceed;
  transform: rotate(45deg);
  top: 50px;
  left:-13px;
 }

 form{
  padding:2.5rem 2.2rem;
  z-index:10;
  overflow:hidden;
  position: relative;
 }

 .title{
  color: #fff;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1;
  margin-bottom: 0.7rem;
 }

 .input-container{
  position: relative;
  margin: 1rem 0 ;
 }

 label{
  display: none;
 }

 .input{
  width:100%;
  outline:none;
  border:2px solid #fff;
  background: none;
  padding: 0.6rem 1.2rem;
  color:#fff;
  font-weight: 500;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
  border-radius: 25px;
  transition: 0.3s;
 }

textarea .input{
  padding:0.8rem 1.2rem;
  min-height:150px;
  border-radius: 22px;
  resize:none;
  overflow-y:auto;
}

label{
  position: absolute;
  top:50%;
  left:15px;
  transform: translate(-50%);
  padding: 0 0.4rem;
  color: #fafafa;
  font-size: 0.9rem;
  font-weight: 400;
  pointer-events: none;
  z-index: 1000;
  transition: 0.5s;
}

.send{
  padding: 0.6rem 1.3rem;;
  background-color: #fff;
  border:2px solid  #fafafa;
  font-size: 0.95rem;
  color: #87ceed;
  line-height: 1;
  border-radius: 25px;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.send:hover{
  background-color: transparent;
  color:#fff
}

.contact-info{
  color: #000;
  text-align: center;
  padding:10px;
}

.contact-info h2{
  font-size: 20px;
  padding: 30px 0;
}

.in{
  content: '\f08c';
}

::placeholder{
  color:#fafafa
}

.footer{
  background-color: #fff;
  color:#000;
  display:flex;
  justify-content: space-between;

}

.can{
  max-width: 400px;
}

.can li{
  list-style-type: none;
  opacity: 0.9;
  padding: 10px;
}

.can h2{
  text-align: center;
}

.can p{
  padding: 10px;
  line-height: 26px;
}

.sm{
  display: flex;
  justify-content: space-evenly;

}
.sm img{
  width:20px;
  cursor: pointer;
}